@import '../../node_modules/@splidejs/splide/dist/css/splide.min.css';
.slider-wrap, .splide, .splide__track, .splide__list, .splide__slide, .slide-wrap, .slide-img {
  height: 100%;
  width: 100% !important;
}

.product-slider .slide-wrap .slide-img {
  background-size: cover;
  background-position: center;
}
.product-slider .splide__arrow {
  top: 0;
  height: 100%;
  width: 90px;
  transform: unset;
  background-color: transparent;
  opacity: 1;
}
.product-slider .splide__arrow svg {
  width: 7px;
  height: 14px;
}
.product-slider .splide__arrow--prev {
  left: 0;
}
.product-slider .splide__arrow--next {
  right: 0;
}

@media (max-width: 991px) {
  .product-slider .slide-wrap .slide-img {
    padding-bottom: 35%;
  }
}
@media (max-width: 575px) {
  .product-slider .slide-wrap .slide-img {
    padding-bottom: 74%;
  }
  .product-slider .splide__arrow {
    width: 60px;
  }
  .product-slider .splide__arrow img {
    width: 40px;
  }
}
.basket {
  position: absolute;
  right: 100px;
  bottom: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  background-color: #fff;
  border-radius: 50%;
  z-index: 50;
}
.basket .counter {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: -3px;
  bottom: -3px;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  font-weight: 700;
  background-color: #1769FF;
}

@media (max-width: 1199px) {
  .basket {
    right: 24px;
    bottom: 24px;
  }
}
.card-section {
  display: flex;
  flex: 1;
  margin-bottom: 120px;
  margin-right: 60px;
}

.card-grid {
  flex: 1;
  display: grid;
  grid-template-columns: 28.3% 21.2% 1fr;
  grid-template-rows: auto 1fr;
  grid-template-areas: "product-name description slider" "product-name settings slider" "product-name settings slider" "back back back";
}
.card-grid .page-name {
  display: flex;
  align-items: center;
}
.card-grid .page-name object {
  margin-right: 15px;
}
.card-grid .product-name-cell {
  grid-area: product-name;
}
.card-grid .description-cell {
  padding-top: 50px;
  grid-area: description;
  margin-bottom: 40px;
}
.card-grid .description-cell .description {
  margin-bottom: 20px;
}
.card-grid .settings-cell {
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  grid-area: settings;
}
.card-grid .settings-cell .settings-title {
  font-size: 14px;
  font-weight: 700;
  margin-bottom: 20px;
}
.card-grid .settings-cell .settings-item {
  margin-bottom: 30px;
}
.card-grid .settings-cell .settings-item:last-child {
  margin-bottom: 0;
}
.card-grid .settings-cell .colors {
  --bs-gutter-x: 20px;
  --bs-gutter-y: 20px;
}
.card-grid .settings-cell .colors .color {
  display: block;
  height: 36px;
  width: 100%;
  opacity: 0.5;
  transition: all 0.3s ease-in-out;
}
.card-grid .settings-cell .colors .color:hover {
  opacity: 1;
}
.card-grid .settings-cell .colors .color.active {
  opacity: 1;
}
.card-grid .settings-cell .radio-group {
  position: relative;
}
.card-grid .settings-cell .radio-group .custom-input {
  position: absolute;
  top: 0;
  left: 0;
  visibility: hidden;
}
.card-grid .settings-cell .radio-group .custom-label {
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: relative;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  opacity: 0.5;
}
.card-grid .settings-cell .radio-group .custom-label:hover {
  opacity: 1;
}
.card-grid .settings-cell .radio-group .custom-input:checked ~ .custom-label {
  background-color: #fff;
  color: #000;
  opacity: 1;
}
.card-grid .settings-cell .btn-cart {
  margin-top: 30px;
  font-weight: 700;
  height: 40px;
  width: 100%;
  transition: all 0.3s ease-in-out;
}
.card-grid .settings-cell .btn-cart:hover {
  color: #000;
  background-color: #fff;
}
.card-grid .settings-cell .btn-cart-link {
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card-grid .settings-cell .button-border {
  border: 1px solid #FFFFFF;
}
.card-grid .slider-cell {
  grid-area: slider;
  margin-left: 60px;
}
.card-grid .back-cell {
  grid-area: back;
}

@media (max-width: 1199px) {
  .card-section {
    margin-right: 0;
  }
}
@media (max-width: 991px) {
  .card-section {
    margin-bottom: 0;
  }

  .card-grid {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto 70px auto;
    grid-template-areas: "slider slider" "product-name settings " "description settings";
  }
  .card-grid .slider-cell {
    margin-left: 0;
    margin-bottom: 40px;
  }
  .card-grid .description-cell {
    padding-top: 0;
    padding-left: 50px;
  }
  .card-grid .description-cell .description {
    margin-right: 30px;
  }
  .card-grid .settings-cell {
    padding-bottom: 0;
    justify-content: flex-start;
  }
  .card-grid .settings-cell .settings {
    margin-bottom: 30px;
  }
  .card-grid .settings-cell .settings .settings-item:last-child .settings-title {
    display: none;
  }
}
@media (max-width: 767px) {
  .card-grid {
    grid-template-rows: auto 50px auto;
  }
  .card-grid .page-name object {
    display: none;
  }
  .card-grid .description-cell {
    padding-left: 0;
  }
  .card-grid .settings-cell {
    margin: 0 15px;
  }
}
@media (max-width: 575px) {
  .card-section {
    height: auto;
    margin-bottom: 0;
  }

  .card-grid {
    grid-template-columns: 1fr;
    grid-template-rows: auto 40px auto auto;
    grid-template-areas: "slider" "product-name" "description" "settings";
  }
  .card-grid .settings-cell .settings {
    text-align: center;
  }
  .card-grid .description-cell {
    margin-bottom: 20px;
  }
  .card-grid .description-cell .description {
    margin-right: 0;
  }
}
.page-wrapper.full-screen .panel main .card-section .card-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.page-wrapper.full-screen .panel main .card-section .card-content > .row {
  flex: 1;
}
.page-wrapper.full-screen .panel main .card-section .right-section {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.page-wrapper.full-screen .panel main .card-section .right-section > .row {
  flex: 1;
}

.card-section .left-section {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card-section .left-section .layout-name {
  display: flex;
  align-items: center;
}
.card-section .left-section .layout-name .logo-star {
  margin-right: 15px;
}
.card-section .left-section .back-link {
  margin-bottom: 10px;
}
.card-section .right-section .slider-wrap {
  margin-left: 40px;
}
.card-section .right-section .product-info {
  padding: 40px 0 25px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.card-section .right-section .description {
  margin-bottom: 50px;
}
.card-section .right-section .description p {
  margin-bottom: 10px;
}
.card-section .right-section .settings {
  margin-bottom: 60px;
}